import React, { useState, useEffect } from "react";
import { Switch, Route, withRouter } from "react-router-dom";

import HomePage from "./pages/homepage/homepage.component";
import GroupHighlight from "./pages/group-highlights/group-highlight.component";

import SignIn from "./components/sign-in/sign-in.component";
import SignUp from "./components/sign-up/sign-up.component";
import Header from "./components/header/header.component";

function App() {
  const [bucket, setBucket] = useState([]);
  const [users_data, setUserData] = useState([]);
  const [isSignIn, setIsSignIn] = useState(false);
  const [currentUser, setCurrentUser] = useState({
    uid: "",
    email: "",

    FirstName: "",
    LastName: "",
    bucket: [],
    highlights: [],
  });

  useEffect(() => {
    const getData = () => {
      const localBucket = JSON.parse(localStorage.getItem("bucket"));
      const localUsers = JSON.parse(localStorage.getItem("users_data"));

      localBucket ? setBucket(localBucket) : setBucket([]);
      localUsers ? setUserData(localUsers) : setUserData([]);
    };
    getData();
  }, []);

  useEffect(() => {
    localStorage.setItem("bucket", JSON.stringify(bucket));
  }, [bucket]);

  useEffect(() => {
    localStorage.setItem("users_data", JSON.stringify(users_data));
  }, [users_data]);

  function editUpdate(newHghlt) {
    let editUser = users_data.filter((i) => i.id !== newHghlt.id);
    setUserData([...editUser, newHghlt]);
    // setCurrentUser({ ...currentUser, highlights: [...editUser, newHghlt] });
  }

  function update(newHghlt) {
    const updatedUserData = [...users_data, newHghlt];
    setUserData(updatedUserData);
    setCurrentUser({
      ...currentUser,
      highlights: [...updatedUserData],
    });
  }

  function remove(id) {
    alert("This highlight will be deleted");

    let updatedUserData = users_data.filter((i) => i.id !== id);

    setUserData(updatedUserData);
  }

  return (
    <div>
      <Header
        bucket={bucket}
        isSignIn={isSignIn}
        users_data={users_data}
        setIsSignIn={setIsSignIn}
        setBucket={setBucket}
        currentUser={currentUser}
        setCurrentUser={setCurrentUser}
        setUserData={setUserData}
      />
      <Switch>
        <Route
          path="/signin"
          render={() => (
            <SignIn
              setIsSignIn={setIsSignIn}
              setCurrentUser={setCurrentUser}
              setBucket={setBucket}
              setUserData={setUserData}
            />
          )}
        />
        <Route
          path="/signup"
          render={() => (
            <SignUp
              setIsSignIn={setIsSignIn}
              setCurrentUser={setCurrentUser}
              setBucket={setBucket}
              setUserData={setUserData}
            />
          )}
        />
        <Route
          exact
          path="/group"
          render={() => (
            <GroupHighlight
              bucket={bucket}
              setBucket={setBucket}
              data={users_data}
              setData={setUserData}
            />
          )}
        />
        <Route
          path="/Affinity"
          render={() => (
            <HomePage
              bucket={bucket}
              setBucket={setBucket}
              data={users_data}
              setData={setUserData}
              update={update}
              editUpdate={editUpdate}
              remove={remove}
            />
          )}
        />
      </Switch>
    </div>
  );
}

export default withRouter(App);
