import React from "react";
import HighLight from "./../../components/highlights/highlight.component";

import "./group-highlight.style.scss";

const GroupHighlight = ({ bucket, data }) => {
  const color = [
    "#4fde4f",
    "#4fdec3",
    "#d54fde",
    "#d54ffe",
    "#e54fde",
    "#f52b5b",
    "#ff9800",
    "#1becd9",
    "#dbef1f",
  ];
  const bucketExist = bucket.map((item) => {
    let temp = data.find((el) => el.type === item);
    return temp ? item : null;
  });
  return (
    <div className="group">
      {bucketExist.map((name) =>
        name ? (
          <div className="container">
            <div className="name">
              <h2>{name}</h2>
            </div>

            <div className="highlights">
              {data
                .filter((item) => item.type === name)
                .map((user) => (
                  <div className="item">
                    <div
                      className="item zoomIn"
                      style={{
                        backgroundColor:
                          color[Math.floor(Math.random() * color.length)],
                        borderRadius: "20px",
                      }}
                    >
                      <HighLight key={user.id} item={user} flag={true} />
                    </div>
                  </div>
                ))}
            </div>
          </div>
        ) : null
      )}
    </div>
  );
};

export default GroupHighlight;
