import React from "react";

import EditImage from "./../../assets/pinceau.svg";
import "./highlight.style.scss";

const HighLight = ({
  item: { id, type, feedback, email, username, name },
  remove,
  setIsWrt,
  flag,
}) => {
  function fun() {
    console.log("id to edit:", id);
    setIsWrt(id);
  }
  return (
    <div className="highlight-item">
      <div key={id} className="item-details">
        <label className="type">{type || username}</label>

        <span className="feedback">{feedback}</span>
        <span className="email">{email}</span>
        <b>
          <span className="name">{name}</span>
        </b>
      </div>
      {!flag ? (
        <div className="icon">
          <div
            className="remove"
            onClick={() => {
              remove(id);
            }}
          >
            {" "}
            &#10005;
          </div>
          <div className="write" onClick={fun}>
            <img alt="edit" src={EditImage} height="25px" width="20px" />
          </div>
        </div>
      ) : null}
    </div>
  );
};

export default HighLight;
