import React from "react";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import { Link } from "react-router-dom";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";

import Box from "@material-ui/core/Box";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";

import { makeStyles } from "@material-ui/core/styles";

import {
  SignInWithGoogle,
  onUserChange,
  auth,
  getUserData,
  writeUserData,
  getCurrentUser,
} from "../../firebase/firebase.utils";

import { useHistory } from "react-router";

const SignIn = ({ setIsSignIn, setBucket, setUserData, setCurrentUser }) => {
  const classes = useStyles();

  const history = useHistory();

  async function GoogleSignIn() {
    await SignInWithGoogle();
    setIsSignIn(true);

    history.push("/Affinity");

    const user_id = await onUserChange();

    try {
      const user = await getUserData(user_id);

      if (Object.keys(user).length !== 0) {
        setCurrentUser(user);
        setBucket(user.bucket);
        setUserData(user.highlights);
      } else {
        const userObj = await getCurrentUser();

        const [first, last] = userObj.displayName.split(" ");
        const obj = {
          uid: user_id,
          FirstName: first,
          LastName: last,
          email: userObj.email,
          bucket: ["Example"],
          highlights: [
            {
              id: 1,
              name: "dummy",
              type: "Example",
              feedback: "just for trial",
            },
          ],
        };

        writeUserData(obj);
        setBucket(obj.bucket);
        setCurrentUser(obj);
        setUserData(obj.highlights);
      }
    } catch (err) {
      console.log(err);
    }
  }

  async function handleSubmit(event) {
    event.preventDefault();
    const data = new FormData(event.currentTarget);

    const resp = await {
      email: data.get("email"),
      password: data.get("password"),
    };

    try {
      const user = await auth.signInWithEmailAndPassword(
        resp.email,
        resp.password
      );

      if (user) {
        setIsSignIn(true);

        alert("Welcome back!");
        const user_id = await onUserChange();
        // console.log("current user's id:", user_id);
        if (user_id) {
          const curr = await getUserData(user_id);
          // console.log(curr);

          setCurrentUser({
            uid: curr.uid,
            FirstName: curr.FirstName,
            LastName: curr.LastName,
            bucket: curr.bucket,
            email: curr.email,
            highlights: curr.highlights,
          });

          // console.log("from signin", currentUser);

          setBucket(curr.bucket);
          setUserData(curr.highlights);
        }
      }
    } catch (error) {
      console.log("error", error);
    }

    history.push("/Affinity");
  }

  return (
    <div className={classes.main}>
      <h1>I have already an account</h1>
      <Box
        sx={{
          marginTop: 8,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
        <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
          <TextField
            margin="normal"
            required
            fullWidth
            label="Email Address"
            name="email"
            autoComplete="email"
            autoFocus
          />
          <TextField
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            autoComplete="current-password"
          />
          <FormControlLabel
            control={<Checkbox value="remember" color="primary" />}
            label="Remember me"
          />
          <Button
            className={classes.button}
            type="submit"
            fullWidth
            variant="contained"
            sx={{ mt: 3, mb: 2 }}
          >
            Sign In
          </Button>
          <Button
            className={classes.button}
            onClick={GoogleSignIn}
            variant="contained"
            sx={{ mt: 3, mb: 2 }}
          >
            Sign In With Google
          </Button>
        </Box>
        <Link to="/signup">
          <h4>if you are new user</h4>
        </Link>
      </Box>
    </div>
  );
};
export default SignIn;

const useStyles = makeStyles({
  main: {
    width: 400,
    margin: 50,
    textAlign: "center",
    marginLeft: "450px !important",
  },
  button: {
    margin: 5,
    width: 400,
  },
});
