import React from "react";
import { Box } from "@material-ui/core";
import { Grid } from "@material-ui/core";
import { Avatar } from "@material-ui/core";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import { Typography } from "@material-ui/core";
import { TextField } from "@material-ui/core";
import { Link } from "@material-ui/core";
import { Button } from "@material-ui/core";
import { FormControlLabel } from "@material-ui/core";
import { Checkbox } from "@material-ui/core";
import { auth, onUserChange } from "./../../firebase/firebase.utils";
import { writeUserData } from "./../../firebase/firebase.utils";
import { makeStyles } from "@material-ui/core/styles";
import { useHistory } from "react-router";
const useStyles = makeStyles({
  main: {
    width: 400,
    margin: 50,
    textAlign: "center",
    marginLeft: "450px !important",
  },
});

export default function SignUp({
  setIsSignIn,
  setBucket,
  setUserData,
  setCurrentUser,
}) {
  const classes = useStyles();
  const history = useHistory();

  const handleSubmit = async (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);

    const obj = {
      email: data.get("email"),

      FirstName: data.get("firstName"),
      LastName: data.get("lastName"),
      bucket: ["Example"],
      highlights: [
        { id: 1, name: "dummy", type: "Example", feedback: "just for trial" },
      ],
    };
    try {
      await auth.createUserWithEmailAndPassword(
        data.get("email"),
        data.get("password")
      );

      alert(`Welcome ${obj.email}!`);
      setIsSignIn(true);
      const user_id = await onUserChange();

      const user = Object.assign(obj, { uid: user_id });
      // console.log(user);
      writeUserData(user);
      setCurrentUser({
        uid: user.uid,
        FirstName: user.FirstName,
        LastName: user.LastName,
        bucket: user.bucket,
        email: user.email,
        highlights: user.highlights,
      });
      // console.log("from signup", currentUser);

      setBucket(user.bucket);
      setUserData(user.highlights);
      history.pushState("/Affinity");
    } catch (error) {
      console.log("error", error);
      // alert(error.message);
    }
  };

  return (
    <div className={classes.main}>
      <h1>New User</h1>
      <Box
        sx={{
          marginTop: 8,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign up
        </Typography>
        <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                autoComplete="fname"
                name="firstName"
                required
                fullWidth
                id="firstName"
                label="First Name"
                autoFocus
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                required
                fullWidth
                id="lastName"
                label="Last Name"
                name="lastName"
                autoComplete="lname"
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                required
                fullWidth
                id="email"
                label="Email Address"
                name="email"
                autoComplete="email"
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                required
                fullWidth
                name="password"
                label="Password"
                type="password"
                id="password"
                autoComplete="new-password"
              />
            </Grid>
            <Grid item xs={12}>
              <FormControlLabel
                control={<Checkbox value="readyToSignUp" color="primary" />}
                label="I am ready to sign up."
              />
            </Grid>
          </Grid>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            sx={{ mt: 3, mb: 2 }}
          >
            Sign Up
          </Button>
          <Grid container justifyContent="flex-end">
            <Grid item>
              <Link href="#" variant="body2">
                Already have an account? Sign in
              </Link>
            </Grid>
          </Grid>
        </Box>
      </Box>
    </div>
  );
}
