import React, { useState } from "react";
import FormInput from "./../form-input/form-input.component";
import "./edit.style.scss";

const EditItem = ({ id, items, update, setIsWrt }) => {
  const [data, setData] = useState({
    name: "",
    type: "",
    feedback: "",
  });
  const { name, type, feedback } = data;
  console.log(id, typeof id);
  const objectToEdit = items.filter(function (item) {
    console.log(item);
    console.log(typeof item.id, typeof idx);
    return item.id === id;
  });
  console.log(objectToEdit);

  const obj = {
    name: objectToEdit[0].name,
    type: objectToEdit[0].type,
    feedback: objectToEdit[0].feedback,
  };
  console.log(obj);

  const handleChange = (event) => {
    const { name, value } = event.target;

    setData({ ...obj, [name]: value });
    console.log(name, value, data);
  };

  const handleSubmit = (event) => {
    event.preventDefault();

    console.log(name, type, feedback);

    let newHghlt = {
      id: id,
      type,
      feedback,
      name,
    };

    update(newHghlt);
    setIsWrt(null);
  };

  return (
    <div className="New-item">
      <h2>Edit Details</h2>
      <form className="row" onSubmit={handleSubmit}>
        <FormInput
          name="name"
          label="Name"
          type="text"
          defaultValue={obj.name}
          handleChange={handleChange}
          required
        />

        <FormInput
          name="type"
          defaultValue={obj.type}
          label="Type"
          type="text"
          handleChange={handleChange}
          required
        />

        <FormInput
          name="feedback"
          defaultValue={obj.feedback}
          label="Feedback"
          type="text"
          handleChange={handleChange}
          required
        />

        <div className="button">
          <button type="submit">Done</button>
        </div>
      </form>
    </div>
  );
};

export default EditItem;
