export const createUserProfileDocument = async (userAuth, addtionalData) => {
  if (!userAuth) return;
  const userRef = firestore.doc(`users/${userAuth.uid}`);

  const snapShot = await userRef.get();

  if (!snapShot.exists) {
    const { displayName, email } = userAuth;
    const createdAt = new Date();

    try {
      userRef.set({
        displayName,
        email,
        createdAt,

        ...addtionalData,
      });
    } catch (err) {
      console.log(err);
    }
  }
  return userRef;
};

firebase.initializeApp(config);
export const database = firebase.database();
export const auth = firebase.auth();
export const firestore = firebase.firestore();

export const onUserChange = async () => {
  let uid;
  await auth.onAuthStateChanged((user) => {
    if (user) {
      uid = user.uid;
    }
  });

  return uid;
};

export const getCurrentUser = () => {
  return new Promise((resolve, reject) => {
    const unsubscribe = auth.onAuthStateChanged((userAuth) => {
      unsubscribe();
      resolve(userAuth);
    }, reject);
  });
};

export const updateUserData = async (user) => {
  const userRef = database.ref("users/" + user.uid);
  // console.log("update user data", user.uid);

  await userRef.update({ highlights: user.highlights });
  await userRef.update({ bucket: user.bucket });
  // console.log(userRef);
};

export const writeUserData = (user) => {
  database
    .ref("users/" + user.uid)
    .set(user)
    .catch((error) => {
      console.log(error.message);
    });
};

export const getUserData = async (uid) => {
  let obj = {};
  await database
    .ref("users/" + uid)
    .once("value", (snap) => {
      // console.log(snap.val());
      obj = Object.assign({}, snap.val());
    })
    .catch((err) => console.log(err));
  return obj;
};

const provider = new firebase.auth.GoogleAuthProvider();

provider.setCustomParameters({ prompt: "select_account" });

export const SignInWithGoogle = () => auth.signInWithPopup(provider);
