import React, { useState } from "react";
import Draggable from "react-draggable";

import "./homepage.style.scss";

import EditItem from "./../../components/Edit/edit.component";
import AddItem from "./../../components/AddItem/additem.component";
import HighLight from "./../../components/highlights/highlight.component";

const HomePage = ({
  bucket,
  setBucket,
  data,
  setData,
  update,
  remove,
  editUpdate,
}) => {
  const [flag, setFlag] = useState(false);
  const [type, setType] = useState("N/A");
  const [isWrite, setIsWrt] = useState(null);
  const [active, SetActive] = useState(0);

  const color = [
    "#4fde4f",
    "#4fdec3",
    "#d54fde",
    "#f52b5b",
    "#ff9800",
    "#1becd9",
    "#dbef1f",
  ];

  function fun() {
    setFlag(!flag);
    console.log(flag);
  }

  function onStart() {
    SetActive(active + 1);
  }
  function onStop() {
    SetActive(active - 1);
  }

  const handleSelect = (event) => {
    const { value } = event.target;

    setType(value);
  };
  const reset = () => {
    setData([]);
    setBucket([]);
  };
  async function sampleExample() {
    try {
      const resp = await fetch("https://jsonplaceholder.typicode.com/users");
      const result = await resp.json();

      setBucket(["Example"]);
      let exampleArray = [];
      result
        .slice(0, 10)
        .map((item) => exampleArray.push({ type: "Example", ...item }));
      console.log(exampleArray[0].type);
      setData(exampleArray);
    } catch (err) {
      console.log(err);
    }
  }
  // function shuffle(array) {
  //   array.sort(() => Math.random() - 0.5);
  //   return array;
  // }

  const dragHandlers = { onStart, onStop };
  const shuffledData = data;
  // const shuffledData = currentUser
  //   ? shuffledItem
  //   : bucket[0] === "Example"
  //   ? shuffledItem
  //   : [];
  return (
    <div className="homepage">
      <div className="upper">
        <div className="search">
          <label className="filter">Filter By:</label>
          <select className="dropdown" onClick={handleSelect}>
            <option default value="N/A">
              select
            </option>
            {bucket.map((name) => (
              <option value={name}>{name}</option>
            ))}
          </select>

          <div>
            <button onClick={() => sampleExample()}>Example</button>
            <button onClick={() => reset()}>Reset</button>
          </div>
        </div>

        <div className="image-container" onClick={fun}>
          <img
            alt=""
            height="50px"
            width="50px"
            src="data:image/png;base64,"
          />
        </div>

        {flag ? (
          <AddItem users={data} update={update} setFlag={setFlag} />
        ) : null}
      </div>
      <div className="container">
        <div className="highlights">
          {type === "N/A"
            ? shuffledData.map((user) => (
                <Draggable {...dragHandlers}>
                  <div
                    className="item zoomIn"
                    style={{
                      backgroundColor:
                        color[Math.floor(user.id % color.length)],
                      borderRadius: "20px",
                    }}
                  >
                    <HighLight
                      key={user.id}
                      item={user}
                      remove={remove}
                      setIsWrt={setIsWrt}
                      flag={false}
                    />
                  </div>
                </Draggable>
              ))
            : data
                .filter((item) => item.type === type)
                .map((user) => (
                  <Draggable {...dragHandlers}>
                    <div
                      className="item zoomIn"
                      style={{
                        backgroundColor:
                          color[Math.floor(user.id % color.length)],
                        borderRadius: "20px",
                      }}
                    >
                      <HighLight
                        key={user.id}
                        item={user}
                        remove={remove}
                        setIsWrt={setIsWrt}
                        setItems={setData}
                        flag={false}
                      />
                    </div>
                  </Draggable>
                ))}
        </div>
      </div>
      {isWrite ? (
        <EditItem
          id={isWrite}
          items={data}
          setIsWrt={setIsWrt}
          update={editUpdate}
          setItems={setData}
          remove={remove}
        />
      ) : null}
    </div>
  );
};

export default HomePage;
