import React, { useState } from "react";
import { Link } from "react-router-dom";
import { auth, updateUserData } from "../../firebase/firebase.utils";
import AddBucket from "./../AddBucket/addbucket.component";

import "./header.style.scss";

const Header = ({
  bucket,
  users_data,
  setBucket,
  currentUser,
  setCurrentUser,
  isSignIn,
  setUserData,
  setIsSignIn,
}) => {
  const [flag, setFlag] = useState(false);

  const addNewBucket = (event) => {
    setFlag(!flag);
  };
  function signout() {
    // console.log("from header", currentUser, bucket);
    setCurrentUser({ ...currentUser, highlights: [...users_data] });
    updateUserData(currentUser);
    auth.signOut();
    setIsSignIn(false);
    setBucket([]);
    setUserData([]);
  }
  function updateBucket(element) {
    // console.log(element);
    const updatedBucket = [...currentUser.bucket, element];
    setBucket(updatedBucket);
    setFlag(false);

    setCurrentUser({
      ...currentUser,
      bucket: [...updatedBucket],
    });
  }

  return (
    <div className="header">
      <Link to="/Affinity" className="Message">
        <h1>Message/Affinity Map</h1>
      </Link>

      <div className="options">
        {isSignIn ? (
          <div className="option" onClick={addNewBucket}>
            New Bucket
          </div>
        ) : null}

        {flag ? <AddBucket update={updateBucket} /> : null}
        <div className="option">
          <Link to="/group" className="link">
            Group Highligts
          </Link>
        </div>
        {!isSignIn ? (
          <div className="option">
            <Link to="/signin" className="link">
              Sign In
            </Link>
          </div>
        ) : (
          <div className="option" onClick={signout}>
            <Link to="/Affinity" className="link">
              Sign Out
            </Link>
          </div>
        )}
      </div>
    </div>
  );
};

export default Header;
